blockquote {
    border-left: 4px solid #007bff; /* 蓝色左边框 */
    padding: 10px 20px; /* 内边距 */
    margin: 20px 0; /* 外边距 */
    background-color: #f9f9f9; /* 背景色 */
    font-style: italic; /* 斜体 */
    color: #555; /* 文字颜色 */
  }